const refs = {
  form: document.querySelector('.js-form'),
  contactsList: document.querySelector('.js-list'),
  removeBtn: document.querySelector('.js-delete-btn'),
};

const markup = ({ name, tel, city }) => {
  return `
          <div class="card border-dark m-3 col-3 position-relative" >
            <div class="card-header">${name}</div>
            <div class="card-body text-dark">
              <h5 class="card-title">${city}</h5>
              <p class="card-text">${tel}</p>
            </div>
            <button type="button" class="btn btn-dark btn-sm border border-0 rounded-circle position-absolute top-0 start-100 translate-middle js-delete-btn" aria-label="Close"><i class="bi bi-trash"></i></button>
          </div>

  `;
};

const contactsService = {
  contactsData: [],

  createContact({ userName, userTel, userCity }) {
    const uid = () => {
      return Math.random().toString(36).substr(2, 9);
    };

    return {
      id: uid(),
      name: userName,
      tel: userTel,
      city: userCity,
    };
  },

  addContact(obj) {
    this.contactsData.push(this.createContact(obj));

    return this.updateContacts();
  },

  updateContacts() {
    return this.contactsData;
  },
};

const onSubmit = event => {
  event.preventDefault();

  const options = {
    userName: event.currentTarget.elements.name.value.trim(),
    userTel: event.currentTarget.elements.tel.value.trim(),
    userCity: event.currentTarget.elements.city.value.trim(),
  };

  if (!options.userName || !options.userTel || !options.userCity)
    return alert('Все поля обязательны к заполнению');

  contactsService.addContact(options);

  const createMarkup = contactsService.contactsData
    .map(contact => markup(contact))
    .join('');

  refs.contactsList.innerHTML = createMarkup;

  refs.form.reset();
};

refs.form.addEventListener('submit', onSubmit);
